"use client";

import { useState } from "react";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Button } from "@/components/ui/button";
import { Textarea } from "@/components/ui/textarea";
import { MessageCircle, ThumbsUp, Heart, SmilePlus, CornerDownRight, MessagesSquare } from "lucide-react";
import { Popover, PopoverContent, PopoverTrigger } from "@/components/ui/popover";

const ReactionIcons = {
  like: ThumbsUp,
  love: Heart,
  laugh: SmilePlus
};

const Comment = ({ comment, onAddReaction, onAddReply, level = 0 }) => {
  const [isReplying, setIsReplying] = useState(false);
  const [replyContent, setReplyContent] = useState("");
  const [showReplies, setShowReplies] = useState(level < 1); // Auto expand first level

  const handleReplySubmit = (e) => {
    e.preventDefault();
    if (replyContent.trim()) {
      onAddReply(comment.id, replyContent);
      setReplyContent("");
      setIsReplying(false);
    }
  };

  const hasReplies = comment.replies && comment.replies.length > 0;

  return (
    <div className={`flex flex-col ${level > 0 ? "ml-12 mt-4" : "mb-6"}`}>
      <div className="p-4 rounded-lg border bg-card">
        <div className="flex items-start gap-4">
          <Avatar>
            <AvatarImage src={comment.avatarUrl} alt={comment.author} />
            <AvatarFallback>{comment.author.slice(0, 2)}</AvatarFallback>
          </Avatar>
          <div className="flex-1">
            <div className="flex items-center justify-between mb-2">
              <h3 className="font-medium">{comment.author}</h3>
              <span className="text-sm text-muted-foreground">
                {comment.date}
              </span>
            </div>
            <p className="text-sm mb-3">{comment.content}</p>
            
            <div className="flex items-center gap-4 text-sm">
              <div className="flex items-center gap-1">
                <Popover>
                  <PopoverTrigger asChild>
                    <Button variant="ghost" size="sm" className="h-auto p-1 text-muted-foreground">
                      React
                    </Button>
                  </PopoverTrigger>
                  <PopoverContent className="w-auto p-2 flex gap-2">
                    <Button 
                      variant="ghost" 
                      size="sm" 
                      className="p-2" 
                      onClick={() => onAddReaction(comment.id, 'like', level > 0)}
                    >
                      <ThumbsUp className="h-4 w-4" />
                    </Button>
                    <Button 
                      variant="ghost" 
                      size="sm" 
                      className="p-2" 
                      onClick={() => onAddReaction(comment.id, 'love', level > 0)}
                    >
                      <Heart className="h-4 w-4" />
                    </Button>
                    <Button 
                      variant="ghost" 
                      size="sm" 
                      className="p-2" 
                      onClick={() => onAddReaction(comment.id, 'laugh', level > 0)}
                    >
                      <SmilePlus className="h-4 w-4" />
                    </Button>
                  </PopoverContent>
                </Popover>
              </div>
              
              <Button 
                variant="ghost" 
                size="sm" 
                className="h-auto p-1 text-muted-foreground"
                onClick={() => setIsReplying(!isReplying)}
              >
                Reply
              </Button>
            </div>
            
            {/* Reactions summary */}
            {Object.entries(comment.reactions).some(([_, count]) => count > 0) && (
              <div className="flex items-center gap-2 mt-3 p-1.5 bg-muted rounded-full text-xs w-fit">
                {Object.entries(comment.reactions).map(([reaction, count]) => {
                  if (count > 0) {
                    const Icon = ReactionIcons[reaction];
                    return (
                      <div key={reaction} className="flex items-center gap-1">
                        <Icon className="h-3 w-3" />
                        <span>{count}</span>
                      </div>
                    );
                  }
                  return null;
                })}
              </div>
            )}
          </div>
        </div>
      </div>
      
      {/* Reply form */}
      {isReplying && (
        <div className="ml-12 mt-3">
          <form onSubmit={handleReplySubmit} className="flex flex-col gap-2">
            <Textarea
              placeholder="Write a reply..."
              value={replyContent}
              onChange={(e) => setReplyContent(e.target.value)}
              className="min-h-[80px]"
            />
            <div className="flex justify-end gap-2">
              <Button 
                type="button" 
                variant="outline" 
                size="sm"
                onClick={() => {
                  setIsReplying(false);
                  setReplyContent("");
                }}
              >
                Cancel
              </Button>
              <Button type="submit" size="sm">Post Reply</Button>
            </div>
          </form>
        </div>
      )}
      
      {/* Replies */}
      {hasReplies && (
        <div className="ml-8 mt-2">
          <Button 
            variant="ghost" 
            size="sm" 
            className="flex items-center gap-1 h-auto p-1 text-muted-foreground"
            onClick={() => setShowReplies(!showReplies)}
          >
            {showReplies ? (
              <span className="flex items-center gap-1">
                <CornerDownRight className="h-3 w-3" /> 
                Hide replies
              </span>
            ) : (
              <span className="flex items-center gap-1">
                <MessagesSquare className="h-3 w-3" /> 
                Show {comment.replies.length} {comment.replies.length === 1 ? 'reply' : 'replies'}
              </span>
            )}
          </Button>
          
          {showReplies && (
            <div className="mt-2">
              {comment.replies.map((reply) => (
                <Comment 
                  key={reply.id} 
                  comment={reply} 
                  onAddReaction={onAddReaction}
                  onAddReply={onAddReply}
                  level={level + 1} 
                />
              ))}
            </div>
          )}
        </div>
      )}
    </div>
  );
};

export default function Comments({ comments }) {
  const [localComments, setLocalComments] = useState(comments || []);
  const [commentInput, setCommentInput] = useState("");
  
  const handleAddComment = (e) => {
    e.preventDefault();
    
    if (commentInput.trim() === "") return;
    
    const newComment = {
      id: Date.now(),
      author: "Guest User",
      avatarUrl: "https://ui-avatars.com/api/?name=Guest+User&background=random",
      date: new Date().toLocaleDateString("en-US", {
        year: "numeric",
        month: "long",
        day: "numeric",
      }),
      content: commentInput,
      reactions: { like: 0, love: 0, laugh: 0 },
      replies: []
    };
    
    setLocalComments([...localComments, newComment]);
    setCommentInput("");
  };
  
  const handleAddReaction = (commentId, reactionType, isReply = false) => {
    setLocalComments(prevComments => {
      return prevComments.map(comment => {
        if (!isReply && comment.id === commentId) {
          return {
            ...comment,
            reactions: {
              ...comment.reactions,
              [reactionType]: (comment.reactions[reactionType] || 0) + 1
            }
          };
        } else if (comment.replies) {
          return {
            ...comment,
            replies: comment.replies.map(reply => {
              if (reply.id === commentId) {
                return {
                  ...reply,
                  reactions: {
                    ...reply.reactions,
                    [reactionType]: (reply.reactions[reactionType] || 0) + 1
                  }
                };
              } else if (reply.replies) {
                return {
                  ...reply,
                  replies: reply.replies.map(nestedReply => {
                    if (nestedReply.id === commentId) {
                      return {
                        ...nestedReply,
                        reactions: {
                          ...nestedReply.reactions,
                          [reactionType]: (nestedReply.reactions[reactionType] || 0) + 1
                        }
                      };
                    }
                    return nestedReply;
                  })
                };
              }
              return reply;
            })
          };
        }
        return comment;
      });
    });
  };
  
  const handleAddReply = (commentId, content) => {
    const newReply = {
      id: Date.now(),
      author: "Guest User",
      avatarUrl: "https://ui-avatars.com/api/?name=Guest+User&background=random",
      date: new Date().toLocaleDateString("en-US", {
        year: "numeric",
        month: "long",
        day: "numeric",
      }),
      content,
      reactions: { like: 0, love: 0, laugh: 0 },
      replies: []
    };
    
    setLocalComments(prevComments => {
      return prevComments.map(comment => {
        if (comment.id === commentId) {
          return {
            ...comment,
            replies: [...(comment.replies || []), newReply]
          };
        } else if (comment.replies) {
          return {
            ...comment,
            replies: comment.replies.map(reply => {
              if (reply.id === commentId) {
                return {
                  ...reply,
                  replies: [...(reply.replies || []), newReply]
                };
              }
              return reply;
            })
          };
        }
        return comment;
      });
    });
  };
  
  return (
    <div className="mt-12 pt-8 border-t">
      <h2 className="text-2xl font-bold mb-8 flex items-center gap-2">
        <MessageCircle className="h-6 w-6" />
        Comments ({localComments.length})
      </h2>
      
      {/* Comment Form */}
      <form onSubmit={handleAddComment} className="mb-8">
        <Textarea
          placeholder="Write a comment..."
          className="mb-4 min-h-[100px]"
          value={commentInput}
          onChange={(e) => setCommentInput(e.target.value)}
        />
        <Button type="submit">Post Comment</Button>
      </form>
      
      {/* Comments List */}
      <div className="space-y-6">
        {localComments.length === 0 ? (
          <p className="text-muted-foreground italic">
            No comments yet. Be the first to share your thoughts!
          </p>
        ) : (
          localComments.map((comment) => (
            <Comment
              key={comment.id}
              comment={comment}
              onAddReaction={handleAddReaction}
              onAddReply={handleAddReply}
            />
          ))
        )}
      </div>
    </div>
  );
} 